/* tslint:disable */

import 'rc-dialog/assets/index.less';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
// use import Dialog from 'rc-dialog'
import Dialog from '../src/DialogWrap';

class App extends React.Component {
  state = {
    password: "",
    showChangePassword: false
  };
  login = () => {
    setTimeout(() => {
      location.href = "/";
    }, 500);
  };

  toggleChangePasswordModal = () => {
    console.clear();
    this.setState({
      showChangePassword: !this.state.showChangePassword
    });
  };

  render() {
    let { password, showChangePassword } = this.state;

    return (
      <div>
        <input placeholder="用户名" />
        <input
          type="password"
          value={password}
          onChange={({ target: { value } }) => {
            console.log("change password:", value);
            this.setState({ password: value });
          }}
          placeholder="密码"
        />
        <button onClick={() => this.login()}>login</button>
        <button onClick={this.toggleChangePasswordModal}>openModal</button>
        <Dialog
          mask={false}
          visible={showChangePassword}
          onCancel={this.toggleChangePasswordModal}
          footer={null}
        />
      </div>
    );
  }
}

ReactDOM.render(
  <App />,
  document.getElementById('__react-content'),
);

/* eslint-enable */
/* tslint:enable */



